import React, { useState, useEffect } from 'react'
import { Grid, makeStyles } from '@material-ui/core'
import { useForm as customForm, Form } from 'src/layout/useForm'
import Controls from 'src/views/utils/controls/Controls'
// import { FormRadioGroup } from 'src/views/utils/controls/FormRadioGroup'

const genderItems = [
  { id: 'male', title: 'Male' },
  { id: 'female', title: 'Female' },
  { id: 'other', title: 'Other' },
]

const initialFValues = {
  id: 0,
  name: '',
  description: '',
  latitude: '',
  longitude: '',
  address: '',
  phone: '',
  city: '',
  state: '',
  // gender: 'male',
}

const useStyles = makeStyles((theme) => ({}))

export const BranchForm = (props) => {
  const { recordForEdit, addOrEdit, cancelForm } = props
  const classes = useStyles()
  const { values, setValues, handleInputChange, resetForm } = customForm(initialFValues)

  useEffect(() => {
    if (recordForEdit != null) {
      if (recordForEdit.id) setValues({ ...recordForEdit })
    }
  }, [recordForEdit])

  const handleSubmit = (e) => {
    e.preventDefault()
    addOrEdit(values, resetForm)
  }

  const handleReset = () => {
    resetForm()
  }

  const handleCancel = () => {
    cancelForm(resetForm)
  }
  return (
    <Form onSubmit={handleSubmit}>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <Controls.FormInput
            label="Nombre"
            name="name"
            value={values.name}
            onChange={handleInputChange}
            required
          />
          <Controls.FormInput
            label="Descripción"
            name="description"
            multiline
            value={values.description}
            onChange={handleInputChange}
            required
          />
          <Controls.FormInput
            label="Latitud"
            type="number"
            name="latitude"
            value={values.latitude}
            onChange={handleInputChange}
            validators={['minNumber:-90', 'maxNumber:90']}
            errorMessages={[
              'La latitud debe ser entre -90 y 90',
              'La latitud debe ser entre -90 y 90',
            ]}
          />
          <Controls.FormInput
            label="Longitud"
            type="number"
            name="longitude"
            value={values.longitude}
            onChange={handleInputChange}
            validators={['minNumber:-180', 'maxNumber:180']}
            errorMessages={[
              'La longitud debe ser entre -180 y 180',
              'La longitud debe ser entre -180 y 180',
            ]}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Controls.FormInput
            label="Teléfono"
            name="phone"
            value={values.phone}
            onChange={handleInputChange}
            required
          />
          <Controls.FormInput
            label="Dirección"
            name="address"
            value={values.address}
            onChange={handleInputChange}
            required
          />
          <Controls.FormInput
            label="Estado"
            name="state"
            value={values.state}
            onChange={handleInputChange}
            required
          />
          <Controls.FormInput
            label="Ciudad"
            name="city"
            value={values.city}
            onChange={handleInputChange}
            required
          />
        </Grid>
        <Grid item xs={12} style={{ textAlign: 'right', paddingTop: 5 }}>
          <Controls.FormButton
            variant="text"
            color="default"
            size="large"
            text="Cancelar"
            onClick={handleCancel}
          />
          <Controls.FormButton
            variant="contained"
            color="primary"
            type="submit"
            size="large"
            text="Guardar"
          />
          {/* <Controls.FormButton
            variant="contained"
            color="default"
            size="large"
            text="Limpiar"
            onClick={handleReset}
          /> */}
        </Grid>
      </Grid>
    </Form>
  )
}
