import React, { useEffect, useMemo, useState } from 'react'
import MaterialTable, { MTableAction, MTableBodyRow } from 'material-table'

import Edit from '@material-ui/icons/Edit'

import LocalPhoneIcon from '@mui/icons-material/LocalPhone'
import MapIcon from '@mui/icons-material/Map'
import CalendarTodayIcon from '@mui/icons-material/CalendarToday'
import LocationCity from '@mui/icons-material/LocationCity'
import DescriptionIcon from '@mui/icons-material/Description'
import BusinessIcon from '@mui/icons-material/Business'
import LocationOnIcon from '@mui/icons-material/LocationOn'
import { Spinner } from 'src/views/utils/Spinner'
import { PaperHeader } from 'src/views/utils/PaperHeader'
import { GridTitle } from 'src/views/utils/material-table/GridTitle'
import { GridAddButton } from 'src/views/utils/material-table/GridAddButton'
import { localizationConfig } from '../../utils/material-table/Localization'
import { useDispatch, useSelector } from 'react-redux'
import { getAllBranches, getAllbranches, updateBranchAPI } from 'src/actions/branches'
import moment from 'moment'
import { tableIcons } from 'src/views/utils/material-table/GridIcons'
import { headerStyle } from 'src/views/utils/material-table/GridHeaderStyle'
import components from 'src/views/utils/material-table/GridComponents'
import ActionsTemplate from 'src/views/utils/material-table/GridActions'
import rowStyle from 'src/views/utils/material-table/GridRowStyle'

const BranchesGrid = (props) => {
  const columnas = [
    {
      title: <GridTitle Icon={BusinessIcon} title="Nombre" />,
      field: 'name',
    },
    {
      title: <GridTitle Icon={DescriptionIcon} title="Descripción" />,
      field: 'description',
    },
    {
      title: <GridTitle Icon={LocationOnIcon} title="Dirección" />,
      field: 'address',
    },
    {
      title: <GridTitle Icon={LocalPhoneIcon} title="Teléfono" />,
      field: 'phone',
    },
    {
      // title: 'Correo Electronico',
      title: <GridTitle Icon={LocationCity} title="Ciudad" />,
      field: 'city',
    },
    {
      // title: 'Correo Electronico',
      title: <GridTitle Icon={MapIcon} title="Estado" />,
      field: 'state',
    },
    {
      // title: 'Correo Electronico',
      title: <GridTitle Icon={CalendarTodayIcon} title="Fecha de registro" />,
      field: 'createdAt',
      type: 'date',
      defaultSort: 'desc',
      render: (rowData) => moment(rowData.createdAt).format('DD/MM/YYYY'),
    },
  ]

  const { setOpenPopup, setRecordForEdit } = props

  const dispatch = useDispatch()
  const branches = useSelector((state) => state.branches)

  useEffect(() => {
    branches.length === 0 && dispatch(getAllBranches())
  }, [])

  return useMemo(
    () => (
      <>
        {branches != null ? (
          // <CCard>
          <>
            <PaperHeader title="Sucursales" />
            <MaterialTable
              localization={localizationConfig}
              icons={tableIcons}
              columns={columnas}
              data={branches}
              actions={ActionsTemplate(
                'Editar Sucursal',
                updateBranchAPI,
                setOpenPopup,
                setRecordForEdit,
                dispatch,
              )}
              components={components(setOpenPopup, setRecordForEdit)}
              // actions={[
              //   {
              //     icon: 'save',
              //     tooltip: 'Save User',
              //     onClick: (event, rowData) => setOpenPopup(true),
              //     isFreeAction: true,
              //   },
              //   {
              //     icon: () => <Edit style={{ color: '#1380C7' }} />,
              //     tooltip: 'Editar sucursal',
              //     onClick: (event, rowData) => {
              //       setOpenPopup(true)
              //       setRecordForEdit(rowData)
              //     },
              //   },
              // ]}

              // components={{
              //   Action: (props) => {
              //     return (
              //       <>
              //         {props.action.icon === 'save' ? (
              //           <GridAddButton onClick={(event) => props.action.onClick(event, props.data)} />
              //         ) : (
              //           <MTableAction {...props} />
              //         )}
              //       </>
              //     )
              //   },
              //   Row: (props) => {
              //     return (
              //       <MTableBodyRow
              //         {...props}
              //         onDoubleClick={(e) => {
              //           setOpenPopup(true)
              //           setRecordForEdit(props.data)
              //         }}
              //       />
              //     )
              //   },
              // }}
              options={{
                search: true,
                draggable: false,
                showTitle: false,
                thirdSortClick: false,
                searchFieldAlignment: 'left',
                actionsColumnIndex: -1,
                searchFieldVariant: 'outlined',
                searchFieldStyle: { margin: 15 },
                headerStyle: headerStyle,
                rowStyle: rowStyle,
              }}
            />
          </>
        ) : (
          <Spinner />
        )}
      </>
    ),
    [branches],
  )
}

export default BranchesGrid
