import { Paper, makeStyles } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { addBranchAPI, updateBranchAPI } from 'src/actions/branches'
import { PopupForm } from 'src/views/utils/controls/PopupForm'
import { BranchForm } from './BranchForm'
import BranchesGrid from './BranchesGrid'

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: theme.spacing(3),
    padding: theme.spacing(3),
  },
  searchInput: {
    width: '75%',
  },
}))

const Branches = () => {
  const dispatch = useDispatch()
  const [openPopup, setOpenPopup] = useState(false)
  const [recordForEdit, setRecordForEdit] = useState(null)
  const classes = useStyles()

  const addOrEdit = (branch, resetForm) => {
    if (branch.id === 0) {
      delete branch.id
      dispatch(addBranchAPI(branch))
    } else {
      delete branch.tableData
      dispatch(updateBranchAPI(branch, branch.id))
    }
    resetForm()
    setRecordForEdit(null)
    setOpenPopup(false)
  }
  const cancelForm = (resetForm) => {
    resetForm()
    setRecordForEdit(null)
    setOpenPopup(false)
  }
  return (
    <>
      <Paper>
        <BranchesGrid setOpenPopup={setOpenPopup} setRecordForEdit={setRecordForEdit} />
      </Paper>
      <PopupForm
        openPopup={openPopup}
        setOpenPopup={setOpenPopup}
        setRecordForEdit={setRecordForEdit}
        title="Sucursal"
      >
        <BranchForm recordForEdit={recordForEdit} addOrEdit={addOrEdit} cancelForm={cancelForm} />
      </PopupForm>
    </>
  )
}
export default Branches
